.nav {
  display: inline-flex;
  justify-content: space-around;
  width: 100%;
  padding: 12px 0;
  margin: auto 0 0 0;
  //outline: 1px solid rebeccapurple;
  position: fixed;
  bottom: 0;
  background-color: rgba(255, 255 ,255, .08);

  &__item {
    display: flex;
    flex: 1 1 0;
    align-items: center;
    color: #c6c6c6;
    text-align: center;
    text-decoration: none;
    font-size: 12px;
    flex-flow: column;
    justify-items: flex-end;
    gap: .5rem;
  }
  &__icon {
    width: 32px;
    height: 32px;
  }
}
