.main-swiper-wrapper {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  //row-gap: 20px;
  //padding: 30px;
}

.main-swiper {
  width: 100%;
  height: 100%;
  max-width: 460px;
  min-width: 280px;
  min-height: 280px;
  max-height: 360px;
  padding: 30px;
  outline: 1px solid salmon;
}

.slide {
  background-color: #F4F4F4;
  border-radius: 20px;
  color: #2F2C3B;
  display: flex;
  align-items: center;
  justify-content: center;
  //margin: 18px;
}

.swiper-pagination-bullet {
  width: 20px;
  height: 14px;
  border-radius: 3px;
}

.swiper-custom-pagination.swiper-pagination-bullets-dynamic {
  //position: unset;
  transform: translateX(0) !important;
}
